import React, { useState, useCallback } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import {
  Container,
  Box,
  Typography,
  IconButton,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  ToggleButtonGroup,  // 
  ToggleButton,       // 
  Fade
} from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers';
import {
  ArrowBack,
  Edit,
  DirectionsWalk,
  DirectionsCar,
  DirectionsTransit,
  EditCalendar as EditIcon,
  Preview as PreviewIcon
} from '@mui/icons-material';
import TimelineView from './TimelineView';
import TimelinePreview from './TimelinePreview';

const TripPlanningPage = () => {
    const location = useLocation();
    const navigate = useNavigate();
    const { selectedPlaces, previousPageData } = location.state || { selectedPlaces: [] };
    const [events, setEvents] = useState([]);
    const [viewMode, setViewMode] = useState('edit');
    

  // 
  const [tripName, setTripName] = useState('My Trip');
  const [peopleCount, setPeopleCount] = useState(1);
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);
  const [transportMode, setTransportMode] = useState('walking');
  const [isEditDialogOpen, setIsEditDialogOpen] = useState(false);

  // 
  const [editValues, setEditValues] = useState({
    tripName,
    peopleCount,
    startDate,
    endDate,
    transportMode,
  });


  const handleEventsUpdate = useCallback((newEvents) => {
    setEvents(newEvents);
  }, []);

  // 
  const handleEditClick = () => {
    setEditValues({
      tripName,
      peopleCount,
      startDate,
      endDate,
      transportMode,
    });
    setIsEditDialogOpen(true);
  };

  // 
  const handleSave = () => {
    setTripName(editValues.tripName);
    setPeopleCount(editValues.peopleCount);
    //  Date 
    setStartDate(editValues.startDate ? new Date(editValues.startDate) : null);
    setEndDate(editValues.endDate ? new Date(editValues.endDate) : null);
    setTransportMode(editValues.transportMode);
    setIsEditDialogOpen(false);
  };

  // 
  const getDateRangeText = () => {
    if (!startDate || !endDate) return 'Dates not set';
    return `${startDate.toLocaleDateString()} - ${endDate.toLocaleDateString()}`;
  };

  // 
  const getTransportText = () => {
    const modes = {
      walking: 'Walking',
      transit: 'Public Transit',
      driving: 'Driving',
    };
    return modes[transportMode] || '';
  };

  return (
    <Container maxWidth={false} sx={{ px: 3 }}> {/*  false  */}
      <Box sx={{ py: 4 }}>
        {/*  */}
        <Box display="flex" alignItems="flex-start" mb={4}>
          <IconButton 
            onClick={() => navigate(-1)} //  navigate(-1)
            sx={{ mr: 2 }}
          >
            <ArrowBack />
          </IconButton>
          
          <Box sx={{ flexGrow: 1 }}>
            <Box display="flex" alignItems="center" mb={1}>
              <Typography variant="h4" component="h1" sx={{ flexGrow: 1 }}>
                {tripName}
              </Typography>
              <IconButton onClick={handleEditClick} size="small">
                <Edit />
              </IconButton>
            </Box>
            
            <Typography variant="subtitle1" color="text.secondary" gutterBottom>
              {peopleCount} {peopleCount === 1 ? 'person' : 'people'}  {getTransportText()}
            </Typography>
            <Typography 
              variant="subtitle1" 
              color="text.secondary" 
              sx={{ 
                mt: 0.5,
                fontSize: '0.9rem',  // 
                opacity: 0.9        // 
              }}
            >
              {getDateRangeText()}
            </Typography>
          </Box>
        </Box>

        <Box sx={{ 
          display: 'flex', 
          justifyContent: 'center',
          mb: 3 
        }}>
          <ToggleButtonGroup
            value={viewMode}
            exclusive
            onChange={(e, newMode) => {
              if (newMode !== null) {
                setViewMode(newMode);
              }
            }}
            aria-label="view mode"
          >
            <ToggleButton value="edit" aria-label="edit mode">
              <EditIcon sx={{ mr: 1 }} />
              Edit
            </ToggleButton>
            <ToggleButton value="preview" aria-label="preview mode">
              <PreviewIcon sx={{ mr: 1 }} />
              Preview
            </ToggleButton>
          </ToggleButtonGroup>
        </Box>

        {/*  */}
        <Box sx={{ 
        minHeight: 'calc(100vh - 200px)',
        bgcolor: 'background.paper',
        borderRadius: 2,
        p: 2,
        overflowX: 'auto'
      }}>
        {/* BoxFade */}
        <Box sx={{ display: viewMode === 'edit' ? 'block' : 'none' }}>
          {startDate && endDate ? (
            <TimelineView
              startDate={startDate}
              endDate={endDate}
              selectedPlaces={selectedPlaces || []}
              onEventsUpdate={handleEventsUpdate}
            />
          ) : (
            <Box sx={{
              height: '100%',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
            }}>
              <Typography color="text.secondary">
                Please set the date range to start planning
              </Typography>
            </Box>
          )}
        </Box>

        <Box sx={{ display: viewMode === 'preview' ? 'block' : 'none' }}>
          <TimelinePreview events={events} />
        </Box>
        </Box>
      </Box>

      {/*  */}
      <Dialog 
        open={isEditDialogOpen} 
        onClose={() => setIsEditDialogOpen(false)}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>Edit Trip Details</DialogTitle>
        <DialogContent>
          <Grid container spacing={3} sx={{ mt: 1 }}>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label="Trip Name"
                value={editValues.tripName}
                onChange={(e) => setEditValues(prev => ({ ...prev, tripName: e.target.value }))}
              />
            </Grid>
            
            <Grid item xs={12}>
              <FormControl fullWidth>
                <InputLabel>Number of People</InputLabel>
                <Select
                  value={editValues.peopleCount}
                  label="Number of People"
                  onChange={(e) => setEditValues(prev => ({ ...prev, peopleCount: e.target.value }))}
                >
                  {[1, 2, 3, 4, 5, 6, 7, 8].map(num => (
                    <MenuItem key={num} value={num}>
                      {num} {num === 1 ? 'person' : 'people'}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <DatePicker
                label="Start Date"
                value={editValues.startDate}
                onChange={(newValue) => setEditValues(prev => ({ ...prev, startDate: newValue }))}
                sx={{ width: '100%' }}
              />
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <DatePicker
                label="End Date"
                value={editValues.endDate}
                onChange={(newValue) => setEditValues(prev => ({ ...prev, endDate: newValue }))}
                sx={{ width: '100%' }}
              />
            </Grid>
            
            <Grid item xs={12}>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                Transportation Mode
              </Typography>
              <ToggleButtonGroup
                value={editValues.transportMode}
                exclusive
                onChange={(e, newMode) => {
                  if (newMode !== null) {
                    setEditValues(prev => ({ ...prev, transportMode: newMode }));
                  }
                }}
                fullWidth
              >
                <ToggleButton value="walking">
                  <DirectionsWalk sx={{ mr: 1 }} />
                  Walking
                </ToggleButton>
                <ToggleButton value="transit">
                  <DirectionsTransit sx={{ mr: 1 }} />
                  Transit
                </ToggleButton>
                <ToggleButton value="driving">
                  <DirectionsCar sx={{ mr: 1 }} />
                  Driving
                </ToggleButton>
              </ToggleButtonGroup>
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setIsEditDialogOpen(false)}>Cancel</Button>
          <Button onClick={handleSave} variant="contained">Save</Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default TripPlanningPage;